<script>
import citiesList from "@/assets/data/citiesList.json";

export default {
  props: ["hoveredDistrict"],
  data() {
    return {
      hoveredID: "",
      positionX: "",
      positionY: "",
    };
  },
  methods: {
    getAreaPosition(area) {
      // console.log(area.getBoundingClientRect());
      let position = area.getBoundingClientRect();
      this.positionX = position.left;
      this.positionY = position.bottom + position.height / 2;
      // this.positionX = (position.left + position.right) / 2;
      // this.positionY = (position.top + position.bottom) / 2;
    },
    hoverCity(event) {
      event.target.classList.toggle("showCity");
      this.hoveredID = event.target.id;
      this.$emit("hoveredArea", this.hoveredArea.cityName);
      this.getAreaPosition(event.target);
    },
    leaveCity(event) {
      event.target.classList.toggle("showCity");
      this.hoveredID = "";
    },
    clickCity(event) {
      this.hoveredID = event.target.id;
      this.$emit("hoveredArea", this.hoveredArea.cityName);
      this.$router.push({
        name: "Search",
        params: { cityLink: this.hoveredArea.cityLink },
      });
    },
  },
  computed: {
    citiesList() {
      return citiesList;
    },
    hoveredArea() {
      return this.citiesList.filter((city) => city.id === this.hoveredID)[0];
    },
    hoveredCityName() {
      if (this.hoveredID === "") {
        return;
      }
      return (
        this.citiesList.filter((city) => city.id === this.hoveredID)[0]
          .cityName || "縣市"
      );
    },
  },
};
</script>

