  }
}

.showCity {
  fill: orange;
  position: relative;
  transform: translate(-5px, -10px);
  &::after {
    content: "";
    background: black;
    width: 100px;
    height: 1px;
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
  }
}

.cityName {
